import uCharts from '../../components/u-charts/u-charts.js';
var _self;
var canvasObj = {};
export default {
	data() {
		return {
			cWidth: '',
			cHeight: '',
			pixelRatio: 1,
			serverData: '',
			itemCount: 30, //x轴单屏数据密度
			sliderMax: 50,
			//countList:[{name:'标题标题', buytime:'2019-7-30', deadtime:'2019-7-30', price:'20' }],
			countList:[],
			newuser:'',
		}
	},
	onLoad(e) {
		_self = this;











		this.cWidth = uni.upx2px(750);
		this.cHeight = uni.upx2px(500);
	},
	onReady() {
		this.getServerData();
	},
	onNavigationBarButtonTap(e) {
		//刷新
		this.getServerData();
	},
	methods: {
		getServerData() {
			uni.showLoading({
				title: "正在加载数据..."
			});
			let headers = {};
			headers['content-type'] = 'application/x-www-form-urlencoded; charset=UTF-8';
			uni.request({
				url: this.$url + '/php/count.php',//此处使用了全局变量拼接url（main.js文件中），关于全局变量官方问答里有
				method: 'POST',//get或post
				header: headers,
				data: {
				},
				success: function(res) {
					let data = res.data;
					if (data.ec === 0) {
						_self.countList=data.user;
						_self.fillData(data);
					} else {
						_self.newuser='暂无数据';
					}
				},
				fail: () => {
					uni.showToast({
						title: '网络错误'
					});
				},
				complete() {
					uni.hideLoading();
				}
			});
		},
		fillData(data) {
			this.serverData = data;
			let LineA = {
				categories: [],
				series: []
			};
			LineA.categories = data.LineA.categories;
			LineA.series = data.LineA.series;
			this.showLineA("canvasLineA", LineA);
		},
		showLineA(canvasId, chartData) {
			canvasObj[canvasId] = new uCharts({
				$this: _self,
				canvasId: canvasId,
				type: 'line',
				fontSize: 11,
				padding:[15,15,0,15],
				legend:{
					show:true,
					padding:5,
					lineHeight:11,
					margin:5,
				},
				dataLabel: false,
				dataPointShape: false,
				background: '#FFFFFF',
				pixelRatio: _self.pixelRatio,
				categories: chartData.categories,
				series: chartData.series,
				animation: false,
				enableScroll: true, //开启图表拖拽功能
				xAxis: {
					disableGrid: false,
					type: 'grid',
					gridType: 'dash',
					itemCount: 5, 
					scrollShow: false, 
					scrollAlign: 'left',
					scrollBackgroundColor:'#F7F7FF',//可不填写，配合enableScroll图表拖拽功能使用，X轴滚动条背景颜色,默认为 #EFEBEF
					scrollColor:'#4191ea',//可不填写，配合enableScroll图表拖拽功能使用，X轴滚动条颜色,默认为 #A6A6A6
				},
				yAxis: {
					//disabled:true
					gridType: 'dash',
					splitNumber: 5,
					min: 0,
					max: 50,
					format: (val) => {
						return val.toFixed(0) + '瓶'
					} //如不写此方法，Y轴刻度默认保留两位小数
				},
				width: _self.cWidth * _self.pixelRatio,
				height: _self.cHeight * _self.pixelRatio,
				dataLabel: true,
				dataPointShape: true,
				extra: {
					lineStyle: 'straight'
				},
			});		
		},
		touchLineA(e) {
			canvasObj['canvasLineA'].scrollStart(e);
		},
		moveLineA(e) {
			canvasObj['canvasLineA'].scroll(e);
		},
		touchEndLineA(e) {
			canvasObj['canvasLineA'].scrollEnd(e);
			//下面是toolTip事件，如果滚动后不需要显示，可不填写
			canvasObj['canvasLineA'].showToolTip(e, {
				format: function(item, category) {
					return category + ' ' + item.name + ':' + item.data
				}
			});
		},
		godetail(rfid){
			uni.navigateTo({
				url: '../other/detail?rfid='+rfid
			});
			console.log(rfid);
		}
	}
}
